.card {
    /* color theme 1*/
    /* background-color: white; */
    /* border: 2px solid #88abd4; */

    /* color theme 2 #232F3E */
    background-color: #374658;

    margin: 10px;
    padding: 20px;
    border-radius: 6px;
    min-height: 220px;
}

.title {
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 1.2em;
    color: #3ab4f2;
    margin-bottom: 5px;
}

.icon {
    margin-right: 15px;
    display: flex;
    align-items: center;
}

.icon svg {
    height: 25px;
    width: 25px;
}

.li {
    margin-left: 40px;
}

.liTitle {
    font-size: 1em;
    font-weight: 700;

    /* color theme 1 default black */
    /* color theme 2 #f7f5ef */
    color: #f7f5ef;
}

.liDescription {
    font-size: 0.9em;
    /* color theme 1*/
    /* color: #666666; */

    /* color theme 2*/
    color: #aaaaaa;
}