.section {
    padding: 100px; 
    background-image: url('../../public/images/asfalt-dark-1920x1080.png');
    background-image: linear-gradient(135deg, #0078aa, transparent), url('../../public/images/asfalt-dark-1920x1080.png');
    background-attachment: fixed;
}

.sectionTitle {
    margin: 40px 10px;
    text-align: center;
    color: #eeeeee;
}

@media (max-width: 768px){
    .section {
        padding: 50px;
    }
}

.plans table {
    border: 1px solid black;
    border-collapse: collapse;
}

.plans table th {
    background-color: #0078aa;
    min-width: 300px;
}

.cardColumn {
    margin: 20px 0 20px;

}

