.section {
    position: relative;
    z-index: 0;
    
    /* padding: 0px 3vw;   */
    background-image: url('../../public/images/asfalt-dark-1920x1080.png');
    background-image: linear-gradient(135deg, #0078aa, transparent), url('../../public/images/asfalt-dark-1920x1080.png');
    background-attachment: fixed;
}

@media (max-width: 975px) {
    .section {
        font-size: 0.8rem;
    }
}

@media (max-width: 795px){
    .section {
        padding: 0;
    }
}

@media (max-width: 746px){
    .section {
        display: none;
        /* font-size: 0.6rem; */
    }
}

.container {
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}

.menubar {
    background-color: #464242;
    color: #cccccc;
    display: flex;
    justify-content: space-between;
    font-size: 0.8rem;
}

.menubar ul {
    display: flex;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-bottom: 0;
}

.menubar li {
    margin-right: 20px;
}

.idemain {
    display: flex;
    height: 50vh;
    /* width: 75vw; */
    /* min-width: 900px; */
}

.sidebar {
    background-color: #3b3838;
    text-align: center;
    color: #cccccc;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.sidebar ul {
    padding: 10px 0 0;
    margin: 0;
}

.sidebar li {
    margin: 10px;
    width: 36px;
    height: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebarActive {
    color: #f7f5ef;
    border-left: 3px solid #f7f5ef;
}

.explorer {
    background-color: #302e2e;
    padding-left: 10px;
    padding-right: 10px;
    min-width: 170px;
    color: #f7f5ef;
    font-size: 0.8em;
}

.explorerTitleBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.8em;
    margin-top: 16px;
    margin-bottom: 8px;
}

.explorerList{
    margin-left: 10px;
}

.explorerList span {
    margin-right: 5px;
}

.explorerListInactive {
    color: #aaaaaa;
}

.workplace {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.tabBar {
    background-color: #302e2e;
    color: #aaaaaa;
    font-size: 0.8em;
    display: flex;
    flex-direction: row;
    
}

.tab {
    background-color: #3b3838;
    padding: 10px;
    display: flex;
    align-items: center;
    margin-bottom: 0;
}

.tab:not(:first-child) {
    border-left: 1px solid black;
}

.tab span {
    margin: 0 5px;
}

.tabActive {
    background-color: #222020;
    color: #f7f5ef;
}

.editor {
    background-color: #222020;
    color: #f7f5ef;
    padding: 30px;
    flex: 1 100%;
}

.typewriterContainer {
    display: inline-block;
}

.typewriter {
    overflow: hidden;
    white-space: nowrap;
    animation: typewriter 10s infinite ease-in, cursorBlink 1s infinite step-end;
    border-right: 0.2em solid transparent;
    /* padding-right: 2px; */
}

@keyframes typewriter {
    0% {
        width: 0;
    }
    40% {
        width: 100%;
    }
    100% {
        width: 100%;
    }
}

@keyframes cursorBlink {
    0% {
        border-color: #ffffff;
    }
    50% {
        border-color: transparent;
    }
    100% {
        border-color: #ffffff;
    }
}

.link {
    color: inherit;
}