header {
    padding: 20px 100px 20px;
    position: absolute;
    right: 0;
    z-index: 99;
}

.headerFixed {
    position: fixed;
    /* top: -50px; */
    animation: showBottomHeader 0.3s ease-in;
}



@keyframes showBottomHeader {
    0% {
        top: -50px;
    }
    100% {
        top: 0px;
    }
}

@media (max-width: 400px) {
    header {
        left: -40px;
    }
    
    nav {
        width: 170px;
    }
}

nav {
    padding: 5px 0;
    background-color: #f6f6f6;
    border-radius: 10px;
    box-shadow: 3px 3px 3px 3px rgba(0, 0, 0, 0.1);
}

nav a {
    margin: 0 20px;
    color: #666666;
    font-weight: bold;
    text-decoration: none;
}