.section {
    padding: 26vh 150px;
    background-color: #f2f4f4;
    /* background-image: url('../../public/images/asfalt-dark-1920x1080.png'); */
    /* background-attachment: fixed; */
    text-align: center;
}

.section h1 {
    margin-bottom: 32px;
}

.section p {
    font-size: 1.5rem;
}

.contactList{
    padding-left: 0;
}


.contactList li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}

.contactList svg {
    color: #0078aa;
    margin-right: 10px;
}

.contactList a {
    color: #0078aa;
}