.card {
    background-color: white;
    color: black;
    border-radius: 8px;
    padding: 20px;
    margin: 5px;
    height: 90%;
    /* min-width: 180px; */
}

.header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

/* .description {
    
} */

.title {
    font-size: 1rem;
    font-weight: 700;
    margin: auto 0;
}

.tag {
    color: #0078aa;
    font-size: 0.8rem;
    margin: auto 5px;
    border: 1px solid #0078aa;
    border-radius: 4px;
    padding: 1px 2px;
}

