.section {
    background-image: url('../../public/images/asfalt-dark-1920x1080.png');
    background-image: linear-gradient(135deg, #0078aa, transparent), url('../../public/images/asfalt-dark-1920x1080.png');
    background-attachment: fixed;
    padding: 150px;
    /* color: #cccccc; */
    color: white;
}

@media (max-width: 991px){
    .section {
        padding: 50px;
    }
}

.header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
}

.header h1 {
    margin: 0;
}

.column {
    margin-bottom: 15px;
}


