.section {
    background-image: url('../../public/images/asfalt-dark-1920x1080.png');
    background-image: linear-gradient(135deg, #0078aa, transparent), url('../../public/images/asfalt-dark-1920x1080.png');
    background-attachment: fixed;
    height: 48vh;
    color: #f7f5ef;
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.section h1 {
    margin: 20px 0;
    text-align: center;
    font-weight: 700;
}

.section h2 {
    text-align: center;
}

.prologue {
    margin: 140px 50px 0;
}

.subscript {
    font-size: 0.8rem;
    text-align: right;
    color: #f7f5ef;
    position: absolute;
    bottom: 20px;
    left: 43vw;
    z-index: 1;
}

.subscript a {
    /* text-decoration: none; */
    color: #0078aa;
}




/* keep the same value with VSCode.module.css: .section display:none mediaquery */
@media (max-width: 746px){
    .prologue {
        margin-top: 70px;
    }
    .subscript {
        position: relative;
        left: auto;
    }
}

@media (max-width: 600px){
    .prologue {
        margin: 50px 0 0;
    }
}

span {
    display: inline-block;
}