.button {
    color: #0078aa;
    text-decoration: none;
    border: 1px solid #0078aa;
    padding: 8px;
    border-radius: 6px;
    width: 125px;
    /* margin: 9px 0; */
    margin: auto 0;
    
}

.button:hover {
    background-color: #0078aa;
    color: white;
    transition: all 0.3s;
}

