.roleCard {
    background-color: #fefefe;
    height: 100%;
    padding: 10px;
    border: 1px solid #cccccc;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.1);
}

.header {
    /* display: block; */
    border-bottom: 1px solid #cccccc;
    margin-bottom: 10px;
}

.description {
    display: inline-block;
    min-height: 150px;
    overflow-y: visible;
    flex: 1;
}

.description ul {
    margin-right: 32px;
}

.more {
    text-align: center;
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.title {
    font-size: 1.5rem;
    text-align: center;
    margin-bottom: 0.5rem;
}

.subtitle {
    font-size: 1rem;
    text-align: center;
    margin-bottom: 0.5rem;
}

.roleDataListItem {
    display: flex;
}

.roleDataListItem span {
    color: green;
    margin-right: 5px;
}

