.section {
    background-image: url('../../public/images/asfalt-dark-1920x1080.png');
    background-image: linear-gradient(135deg, #0078aa, transparent), url('../../public/images/asfalt-dark-1920x1080.png');
    background-attachment: fixed;
    /* height: 48vh; */
    color: #f7f5ef;
    padding: 150px 150px 100px;

}

@media (max-width: 975px){
    .section {
        padding: 100px;
    }
}

@media (max-width: 680px) {
    .section {
        padding: 100px 50px;
    }
}

.section h1 {
    font-size: 2.5rem;
    margin-bottom: 25px;
}

.list {
    padding-left: 0px;
    /* background-color: #f7f5ef; */
    /* border-radius: 5px; */
    padding: 15px;
    margin-bottom: 35px;
}

.list li {
    margin-bottom: 10px;
}

.title {
    background-color: #f7f5ef;
    border-radius: 5px;
    padding: 5px 10px;
    color: #0078aa;
    font-size: 0.8em;
    margin-right: 10px;
    font-weight: 700;
}